          // {dp: '沃尔玛祁连山路店', xe: '1400', tq: '1230', tb: '-55.4', pc: '20.6', zb: '40.7'},
          // {dp: '东川路8店', xe: '1200', tq: '1800', tb: '65.4', pc: '23.6', zb: '50.7'}
        ]
      },
      zgsQsfxTitle: '趋势分析',
      dpQsfxTitle: '趋势分析',
      zgsDppmTitle: '店铺排名',
      dpJgfxTitle: '结构分析',
      zgsXsfxIndex: 0,
      zgsDppmIndex: 0,
      zgsQsfxIndex: 0,
      dpXsfxIndex: 0,
      dpQsfxIndex: 0,
      dpJgfxIndex: 0, // 店铺结构分析，tabs的索引
      jgfxTypeList: [{name: '大类', value: '大类'}, {name: 'SPU', value: 'SPU'}],
      jgfxTypeListDay: [{name: 'SPU', value: 'SPU'}],
      jgfxTableDataInit: [],
      jgfxTableData: {
        tableHeader: [
          {name: '名称', prop: 'dl', needOrder: false, order: '0', isPresent: false, needArrow: false},
          {name: '销额', prop: 'xe', needOrder: true, order: '0', isPresent: false, needArrow: false},
          {name: '同期', prop: 'tq', needOrder: true, order: '0', isPresent: false, needArrow: false},
          {name: '同比', prop: 'tb', needOrder: true, order: '0', isPresent: true, needArrow: true},
          {name: '毛利率', prop: 'mlv', needOrder: true, order: '0', isPresent: true, needArrow: true, newLine: false},
          {name: '偏差', prop: 'mlv', needOrder: true, order: '0', isPresent: true, needArrow: true},
          {name: '占比', prop: 'zb', needOrder: true, order: '0', isPresent: true, needArrow: false}
        ],
        tableContent: [
          // {dl: '套件', sl: '1600', xe: '1600', tq: '1690', tb: '-35.4', zb: '10.7'},
          // {dl: '被芯', sl: '1400', xe: '1700', tq: '1790', tb: '35.4', zb: '-30.7'},
          // {dl: '凉席', sl: '1200', xe: '1800', tq: '1690', tb: '-35.4', zb: '20.7'},
          // {dl: '夏被', sl: '1600', xe: '1900', tq: '1390', tb: '35.4', zb: '60.7'}
        ]
      },
      jgfxTableDataHeaderMon: [
        {name: '名称', prop: 'dl', needOrder: false, order: '0', isPresent: false, needArrow: false},
        // {name: '数量', prop: 'sl', needOrder: true, order: '2', isPresent: false, needArrow: false},
        {name: '销额', prop: 'xe', needOrder: true, order: '0', isPresent: false, needArrow: false},
        {name: '同期', prop: 'tq', needOrder: true, order: '0', isPresent: false, needArrow: false},
        {name: '同比', prop: 'tb', needOrder: true, order: '0', isPresent: true, needArrow: true},
        {name: '毛利率', prop: 'mlv', needOrder: true, order: '0', isPresent: true, needArrow: true, newLine: false},
        {name: '占比', prop: 'zb', needOrder: true, order: '0', isPresent: true, needArrow: false}
      ],
      selectedDpName: '',
      selectedJgfxType: 'SPU',
      dpJgfxTypeIndex: 0
    }
  },
  computed: {
    dateStr () {
      return this.$store.state.dateStr
    },
    dateStrM () {
      return this.$store.getters.dateStrM
    },
    dateStrJ () {
      return this.$store.getters.dateStrJ
    },
    dpLineOption () {
      const option = {
        color: ['#FFBE14', '#326DFF'],
        'legend': {
          show: true,
          textStyle: {
            fontFamily: 'PingFangSC-Regular',
            color: '#71768C',
            fontSize: px2px(24)
          }
        },
        'xAxis': [
          {
            'type': 'category',
            'name': '',
            interval: 0,
            nameLocation: 'start',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24),
              padding: [0, 0, px2px(-30), px2px(-80)]
            },
            'data': [],
            'axisPointer': {
              'type': 'shadow'
            },
            'axisLabel': {
              'show': true,
              interval: 1,
              'textStyle': {
                'color': '#71768C'
              },
              fontSize: px2px(24)
            },
            'axisLine': {
              'show': false
            },
            axisTick: {
              'show': false
            }
          }
        ],
        'yAxis': [
          {
            'type': 'value',
            'name': '达成',
            'nameTextStyle': {
              'color': '#71768C',
              fontSize: px2px(24)
            },
            'show': true,
            'axisLabel': {
              show: true,
              fontSize: px2px(24),
              'textStyle': {
                'color': '#71768C'
              },
              formatter: '{value}%'
            },
            'axisLine': {
              show: false,
              lineStyle: {
                color: '#ECEFF2'
              }
            },
            'splitLine': {
              show: true,
              lineStyle: {
                color: '#213A6B'
              }
            }
          }
        ],
        series: [
          {
            type: 'line',
            name: '实际',
            smooth: true,
            symbol: 'none',
            yAxisIndex: 0,
            lineStyle: {
              color: '#FFBE14',
              type: 'solid',
              width: px2px(2)
            },
            data: []
          },
          {
            type: 'line',
            name: '目标',
            yAxisIndex: 0,
            symbol: 'none',
            smooth: true,
            lineStyle: {
              color: '#326DFF',
              type: 'solid',
              width: px2px(2)
            },
            data: []
          }]
      }
      const labelList = []
      const sjList = []
      const mbList = []
      for (let item of this.dpLineData) {
        labelList.push(item.label)
        sjList.push(item.sj)
        mbList.push(item.mb)
      }
      option.xAxis[0].data = labelList
      option.series[0].data = sjList
      option.series[1].data = mbList
      return option
    },
    selectedDwName () {
      return this.$store.state.selectedDwName
    }
  },
  methods: {
    tabsType ({activeValue, name, type, index}) {
      if (type === 'sale-zgs-xsfx') {
        this.zgsXsfxIndex = index
        this.dealZgsXsfxTableData()
      } else if (type === 'sale-dp-xsfx') {
        this.dpXsfxIndex = index
        this.$eventBus.$emit('resetTypeList')
        if (this.dpXsfxIndex === 0) {
          this.selectedJgfxType = 'SPU'
        } else {
          this.selectedJgfxType = '大类'
        }
        this.dealDpXsfxTableData()
      } else if (type === 'sale-zgs-dppm') {
        this.zgsDppmIndex = index
        this.dealZgsDppmTableData()
      } else if (type === 'sale-jgfx-up') {
        this.dpJgfxIndex = index
        if (this.dpJgfxIndex === 0) {
          this.dpJgfxTypeIndex = 0
        }
        // this.dealDpjgfxTableData()
        this.getJgfxTableData(this.selectedDpName)
      } else if (type === 'sale-zgs-qsfx') {
        this.zgsQsfxIndex = index
        this.dealZgsQsfxLineData()
      } else if (type === 'sale-dp-qsfx') {
        this.dpQsfxIndex = index
        this.dealDpQsfxLineData()
      }
    },
    changeAllType ({btn, index}) {
      if (index === 0) {
        this.showZgs = true
      } else {
        this.showZgs = false
      }
    },
    changeTableOrder ({header, type}) {
      let tempTable = {tableHeader: [], tableContent: []}
      if (type === 'sale-dp-xsfx') {
        // 这个来分辨哪个表格需要排序
        tempTable = _.cloneDeep(this.dpXsfxTableData)
      } else if (type === 'sale-zgs-xsfx') {
        tempTable = _.cloneDeep(this.zgsXsfxTableData)
      } else if (type === 'sale-zgs-dppm') {
        tempTable = _.cloneDeep(this.zgsDppmTableData)
      } else if (type === 'sale-dp-jgfx') {
        tempTable = _.cloneDeep(this.jgfxTableData)
      }
      const headerList = []
      let tableContent = []
      for (let item of tempTable.tableHeader) {
        if (header.order === '0') {
          // 说明想要升序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '1'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        } else if (header.order === '1') {
          // 说明之前是升序，想要降序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '2'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        } else if (header.order === '2') {
          // 说明之前是降序，想要升序
          if (item.needOrder && header.prop === item.prop) {
            item.order = '1'
          } else if (item.needOrder && header.prop !== item.prop) {
            item.order = '0'
          }
        }
        headerList.push(item)
      }
      if (header.order === '0') {
        // 说明想要升序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, true))
      } else if (header.order === '1') {
        // 说明之前是升序，想要降序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, false))
      } else if (header.order === '2') {
        // 说明之前是降序，想要升序
        tableContent = tempTable.tableContent.sort(compareSort(header.prop, true))
      }
      if (type === 'sale-dp-xsfx') {
        // 这个来分辨哪个表格需要排序
        this.dpXsfxTableData.tableHeader = headerList
        this.dpXsfxTableData.tableContent = tableContent
      } else if (type === 'sale-zgs-xsfx') {
