    // 直营销售分析的数据
    async getZgsXsfxTableData () {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.saleCompany.getZgsXsfxTableData,
        {dateStrM: this.dateStrM, dateStrJ: this.dateStrJ})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.zgsXsfxTableDataInit = result
        // 要根据选择的月，季，年累，年，进行数据的整理
        this.dealZgsXsfxTableData()
      }
    },
    dealZgsXsfxTableData () {
      const list = []
      let dataList = []
      if (this.zgsXsfxIndex === 0) {
        dataList = this.zgsXsfxTableDataInit.resM || []
      } else if (this.zgsXsfxIndex === 1) {
        dataList = this.zgsXsfxTableDataInit.resQ || [] // 季
      } else if (this.zgsXsfxIndex === 2) {
        dataList = this.zgsXsfxTableDataInit.resA || [] // 年累
      } else if (this.zgsXsfxIndex === 3) {
        dataList = this.zgsXsfxTableDataInit.resY || []
      }
      for (let item of dataList) {
        const obj = {}
        obj.zgs = item.distribution_name
        obj.dc = (initNum(item.yield_rate) * 100).toFixed(1)
        obj.dcpm = formatNumber(initNum(item.yield_rate_row), 0)
        obj.tb = (initNum(item.output_amt_yoy) * 100).toFixed(1)
        obj.tbpm = formatNumber(initNum(item.output_amt_yoy_row), 0)
        list.push(obj)
      }
      if (list.length > 0) {
        this.zgsXsfxTableData = {
          tableHeader: [
            {name: '子公司', prop: 'zgs', needOrder: false, order: '0', isPresent: false, needArrow: false},
            {name: '达成', prop: 'dc', needOrder: true, order: '2', isPresent: true, needArrow: false},
            {name: '达成排名', prop: 'dcpm', needOrder: true, order: '0', isPresent: false, needArrow: false},
            {name: '同比', prop: 'tb', needOrder: true, order: '0', isPresent: true, needArrow: true},
            {name: '同比排名', prop: 'tbpm', needOrder: true, order: '0', isPresent: false, needArrow: false}
          ],
          tableContent: list.sort(compareSort('dc', false))
        }
      } else {
        this.zgsXsfxTableData.tableContent = []
      }
    },
    // 查询子公司趋势分析线图数据
    async getZgsQsfxLineData (companyName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.saleCompany.getZgsQsfxLineData,
        {dateStrM: this.dateStrM, subName: companyName})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.zgsQsfxLineDataInit = result.res
        this.dealZgsQsfxLineData()
      }
    },
    dealZgsQsfxLineData () {
      const list = []
      for (let item of this.zgsQsfxLineDataInit) {
        const obj = {}
        obj.label = item.period_wid
        obj.dc = initNum(item.yield_rate) * 100
        if (this.zgsQsfxIndex === 0) {
          obj.tb = initNum(item.output_amt_yoy) * 100
        } else {
          obj.tb = initNum(item.margin_rate) * 100
        }
        obj.nljdc = initNum(item.nl_yield_rate) * 100
        list.push(obj)
      }
      if (list.length > 0) {
        this.zgsQsfxLineData = list
      } else {
        this.zgsQsfxLineData = []
      }
    },
