      }
    },
    // 查询 店铺，销售概览数据
    async getDpXsglTableData (companyName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.saleCompany.getDpXsglTableData,
        {dateStrD: this.dateStr, dateStrM: this.dateStrM, dateStrJ: this.dateStrJ, subName: companyName})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        const list = []
        for (let item of result.res) {
          const obj = {}
          obj.time = item.period_wid
          if (item.period_wid === '本日') {
            obj.mb = '--'
            obj.dc = '--'
            obj.tb = '--'
            obj.xe = formatNumber(initNum(item.output_amt), 2)
            obj.mlv = (initNum(item.margin_rate) * 100).toFixed(1)
          } else {
            obj.mb = formatNumber(initNum(item.target_amt), 2)
            obj.xe = formatNumber(initNum(item.output_amt), 2)
            obj.dc = (initNum(item.yield_rate) * 100).toFixed(1)
            obj.tb = (initNum(item.output_amt_yoy) * 100).toFixed(1)
            obj.mlv = (initNum(item.margin_rate) * 100).toFixed(1)
          }
          list.push(obj)
        }
        if (list.length > 0) {
          this.dpXsglTableData = {
            tableHeader: [{name: '时间', prop: 'time', needOrder: false, order: '0', isPresent: false, needArrow: false},
              {name: '目标', prop: 'mb', needOrder: false, order: '2', isPresent: false, needArrow: false},
              {name: '销额', prop: 'xe', needOrder: false, order: '0', isPresent: false, needArrow: false},
              {name: '达成', prop: 'dc', needOrder: false, order: '0', isPresent: true, needArrow: false},
              {name: '同比', prop: 'tb', needOrder: false, order: '0', isPresent: true, needArrow: true},
              {name: '毛利率', prop: 'mlv', needOrder: false, order: '0', isPresent: true, needArrow: false, newLine: false}
            ],
            tableContent: list.sort(compareSort('mb', true))
          }
        } else {
          this.dpXsglTableData = {
            tableHeader: [{name: '时间', prop: 'time', needOrder: false, order: '0', isPresent: false, needArrow: false},
              {name: '目标', prop: 'mb', needOrder: false, order: '2', isPresent: false, needArrow: false},
              {name: '销额', prop: 'xe', needOrder: false, order: '0', isPresent: false, needArrow: false},
              {name: '达成', prop: 'dc', needOrder: false, order: '0', isPresent: true, needArrow: false},
              {name: '同比', prop: 'tb', needOrder: false, order: '0', isPresent: true, needArrow: true},
              {name: '毛利率', prop: 'mlv', needOrder: false, order: '0', isPresent: true, needArrow: false, newLine: false}
            ],
            tableContent: []
          }
        }
      }
    },
    // 查询 店铺 销售分析 表格数据
    async getDpXsfxTableData (companyName) {
      const {__statusCode, data} = await apiService.getDataFromInterface(apiService.saleCompany.getDpXsfxTableData,
        {dateNum: getMonthDayNum(this.dateStr), dateStrD: this.dateStr, dateStrM: this.dateStrM, dateStrJ: this.dateStrJ, subName: companyName})
      if (Object.is(__statusCode, '1')) {
