    /*
    这里还要请求大类的区分
     */
    async getJgfxTableData (dpName) {
      let url = ''
      if (this.dpJgfxIndex === 0) {
        // 说明看的是日 spu 数据
        url = apiService.saleCompany.getDpjgfxTableSpuDayData
      } else if (this.dpJgfxIndex > 0 && this.dpJgfxTypeIndex === 0) {
        // 说明看的是非日的大类数据
        url = apiService.saleCompany.getDpjgfxTableDlData
      } else if (this.dpJgfxIndex > 0 && this.dpJgfxTypeIndex === 1) {
        // 说明看的是非日的SPU数据
        url = apiService.saleCompany.getDpjgfxTableSpuData
      }
      const {__statusCode, data} = await apiService.getDataFromInterface(url,
        {dateNum: getMonthDayNum(this.dateStr), dateStrD: this.dateStr, dateStrM: this.dateStrM, dateStrJ: this.dateStrJ, cusName: dpName})
      if (Object.is(__statusCode, '1')) {
        const result = JSON.parse(data)
        this.jgfxTableDataInit = result
        this.dealDpjgfxTableData()
      }
    },
    dealDpjgfxTableData () {
      const list = []
      let dataList = []
      if (this.dpJgfxIndex === 0) {
        dataList = this.jgfxTableDataInit.res || []
      } else if (this.dpJgfxIndex === 1) {
        dataList = this.jgfxTableDataInit.resM || []
      } else if (this.dpJgfxIndex === 2) {
        dataList = this.jgfxTableDataInit.resQ || []
      } else if (this.dpJgfxIndex === 3) {
        dataList = this.jgfxTableDataInit.resA || []
      } else if (this.dpJgfxIndex === 4) {
        dataList = this.jgfxTableDataInit.resY || []
      }
      if (this.dpJgfxIndex === 0) {
        for (let item of dataList) {
          const obj = {}
          obj.dl = item.type_name
          obj.xe = formatNumber(initNum(item.output_amt), 2)
          obj.tq = formatNumber(initNum(item.output_amt_lym), 2)
          obj.tb = (initNum(item.output_amt_yoy) * 100).toFixed(1)
          obj.mlv = (initNum(item.margin_rate) * 100).toFixed(1)
          obj.pc = (initNum(item.deviation) * 100).toFixed(1)
          obj.zb = (initNum(item.rotio_rate) * 100).toFixed(1)
          list.push(obj)
        }
      } else {
        for (let item of dataList) {
          const obj = {}
          if (this.dpJgfxTypeIndex === 0) {
            obj.dl = item.SUPPER_TYPE_NAME
          } else {
            obj.dl = item.SPU
          }
          obj.xe = formatNumber(initNum(item.output_amt), 2)
          // obj.sl = formatNumber(initNum(item.output_qty), 0)
          obj.tq = formatNumber(initNum(item.output_amt_lym), 2)
          obj.tb = (initNum(item.output_amt_yoy) * 100).toFixed(1)
          obj.mlv = (initNum(item.margin_rate) * 100).toFixed(1)
          obj.zb = (initNum(item.rotio_rate) * 100).toFixed(1)
          list.push(obj)
        }
      }
      const headerList = [
        {name: '名称', prop: 'dl', needOrder: false, order: '0', isPresent: false, needArrow: false},
        {name: '销额', prop: 'xe', needOrder: true, order: '0', isPresent: false, needArrow: false},
        {name: '同期', prop: 'tq', needOrder: true, order: '0', isPresent: false, needArrow: false},
        {name: '同比', prop: 'tb', needOrder: true, order: '0', isPresent: true, needArrow: true},
        {name: '毛利率', prop: 'mlv', needOrder: true, order: '0', isPresent: true, needArrow: true, newLine: false},
        {name: '偏差', prop: 'pc', needOrder: true, order: '0', isPresent: true, needArrow: true},
        {name: '占比', prop: 'zb', needOrder: true, order: '0', isPresent: true, needArrow: false}
      ]
      this.jgfxTableData = {
        tableHeader: this.dpJgfxIndex === 0 ? headerList : this.jgfxTableDataHeaderMon,
        tableContent: list.sort(compareSort('xe', false))
      }
    }
  },
  created () {
    this.$store.commit('updateHeaderTitle', '销售')
    this.$store.commit('updateShowIcon', true)
    this.$store.commit('updateDateStr', this.$store.state.dateStrInit)
    this.$store.dispatch('updateUserOption', {USER_ACCOUNT: window.userAccount, EXAMINATION_SUBJECT: '直营', LEVEL: '子公司', URL: '销售'})
    this.getAllData()
  },
  mounted () {
    this.$eventBus.$on('userSelected', this.getAllData)
  },
  beforeDestroy () {
    this.$eventBus.$off('userSelected', this.getAllData)
  },
  /**
  watch: {
    dateStr: {
      handler () {
        this.getAllData()
      }
    },
    selectedDwName: {
      handler () {
        this.zgsQsfxTitle = '趋势分析-' + this.selectedDwName
        this.zgsDppmTitle = '店铺排名-' + this.selectedDwName
        // 查询 子公司趋势分析线图
        this.getZgsQsfxLineData(this.selectedDwName)
        // 查询子公司 店铺排名线图
        this.getZgsDppmTableData(this.selectedDwName)
        // 查询该子公司的 所有店铺的销售概览
        this.getDpXsglTableData(this.selectedDwName)
        // 查该子公司下的店铺的销售概览
        this.getDpXsfxTableData(this.selectedDwName)
      }
    }
  },
   **/
  components: {
    pageTitle,
    SwitchBtnList,
    MinTitle,
    BarLineChart,
    TableList,
    TypeList,
    Tabs
  }
}
</script>

